import { createStore } from "vuex";
import { setToken, removeToken, getToken } from "@/composables/auth";
import axios from "axios";
import { params } from "@/store/store";
//  store 
const store = createStore({
  state() {
    return {
      //
      user: {},
    };
  },
  mutations: {
    //
    SET_USERINFO(state, user) {
      state.user = user;
    },
  },
  actions: {
    //
    // "login"  Vuex action
    // "login" ~/api/manager
    login({ commit }, { name, password }) {
      return new Promise((resolve, reject) => {
        axios
          .post("/api/auth/login", {
            name,
            password,
          })
          .then((res) => {
            console.log("token", res.token);
            //token
            setToken(res.token);
            resolve(res);
          })
          .catch((err) => reject(err));
      });
    },
    //
    //commitmutation
    getInfo({ commit }) {
      const token = getToken();
      //Promise
      return new Promise((resolve, reject) => {
        axios({
          url: "/api/auth/getInfo",
          method: "post",
          headers: {
            "Content-Type": "application/json",
            Authorization: "Bearer " + token,
          },
          data: {},
        })
          .then(function (resp) {
            console.log("", resp.data.data);
            params.username = resp.data.data.name;
            params.roleId = resp.data.data.role_id;
            params.realname = resp.data.data.real_name;
            if (resp.data.data.role_id.includes("83")) {
              params.role = "";
            } else {
              params.role = "";
            }
            params.isLogin = true;
            params.token = token;
            resolve(resp);
          })
          .catch((err) => reject(err));
      });
    },
    //
    logout({ commit }) {
      //cookietoken  auth.js
      removeToken();
      //
      commit("SET_USERINFO", {});
    },
  },
});
export default store;
